@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100&display=swap);
body {
  background-color: rgb(37, 57, 99);
}




.search-bar {
  display: flex;
  flex-direction: row;
  border: none;
  font-weight: bold;
  font-family: 'Roboto', sans-serif;
  font-weight: bolder;
  font-size: 16px;
  margin-bottom: 10px;
  background-color: rgb(102, 129, 187);
  color: white;
  height: 40px;
}

.inline-child {
  align-self: flex-start;
}

.arrow-icons {
  align-self: flex-start;
  margin-left: 5px;
  border: 1px solid;
  background-color: rgb(63, 94, 163);
}

.wrapper {
  display: flex;
  flex-direction: row;
  margin-top: 10px;
  padding-left: 1%;
  z-index: 1;
  flex-grow: 1;
  align-self: flex-start;
  align-items: flex-start;
}

.stock-search {
  border: none;
  display: inline-block;
  box-sizing: border-box;
  outline: none;
  font-weight: bolder;
  font-family: 'Roboto', sans-serif;
  font-size: 14px;
}

.button {
  align-items: center;
  display: inline-block;
  background-color: rgb(63, 94, 163);
  color: white;
  border: none;
  margin-left: 10px;
  font-family: 'Roboto', sans-serif;
  font-weight: bolder;
  font-size: 16px;
}

.auto-complete-parent {
  width: 20vw;
  display: inline-block;
  border: 1px solid grey;
  box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.1), 0 2px 4px 1px rgba(0, 0, 0, 0.18);
  background-color: white;
  color: black;
  font-size: 12px;
}

.auto-complete-list {
  list-style-type: none;
  text-align: left;
  margin: 5px 0px 0px 0px;
  padding: 0px;
  border-top: 1px solid grey;
}

.auto-complete-list li:hover {
  background:rgb(63, 94, 163);
  cursor: pointer;
  color: white;
}

ul:empty {
  display: none;
}

.sample-size {
  font-family: 'Roboto', sans-serif;
  font-weight: bolder;
  width: 40px;
  text-align: center;
  border: none;
}

.news-articles {
  display: flex;
  flex-direction: column;
  border: none;
  margin: 10px 0px 10px 0px;
  padding: 10px 10px 10px 10px;
  background-color: rgb(102, 129, 187);
  color: white;
}

.news-header {
  background-color: rgb(63, 94, 163);
  padding: 30px 20px 30px 20px;
  /* font-family: 'Playfair Display', sans-serif; */
  /* font-family: 'Rubik', sans-serif; */
  font-family: 'Roboto', sans-serif;
  font-weight: bold;
  font-size: 25px;
}

.news-image {
  max-width: 240px;
  max-height: 180px;
  width: 19%;
  height: auto;
  float: left;
  padding-right: 1%;
}

.hyperlink {
  color: white;
  /* font-family: 'Playfair Display', serif; */
  /* font-family: 'Rubik', sans-serif; */
  font-family: 'Roboto', sans-serif;
  font-weight: bolder;
  font-size: 20px;
  color:rgb(37, 57, 99);
}

.news-text {
  /* font-family: 'Montserrat', sans-serif; */
  font-family: 'Roboto', sans-serif;
  font-weight: bold;
  font-size: 14px;
}

.news-source {
  /* font-family: 'Montserrat', sans-serif; */
  font-family: 'Roboto', sans-serif;
  font-size: 14px;
  font-weight: bold;
  font-style: italic;
}


[id*="news-list"] {
  display: flex;
  flex-direction: row;
  width: 95%;
  margin-bottom: 20px;
  padding: 10px;
  align-items: flex-start;
  border: 1px solid grey;
  box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.1), 0 2px 4px 1px rgba(0, 0, 0, 0.18);
  cursor: pointer;
}

[id*="hidden-news"] {
  display: none;
  width: 95%;
  margin-bottom: 20px;
  padding: 10px;
  align-items: flex-start;
  border: 1px solid grey;
  box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.1), 0 2px 4px 1px rgba(0, 0, 0, 0.18);
  cursor: pointer;
}

#see-more {
  background-color: rgb(63, 94, 163);
  color: white;
  border: none;
  width: 20%;
  margin: auto;
}

